body{
  background:url(/static/media/particles_bg.537c730e.png) repeat
}

.dark body{
  background:url(/static/media/particles_dark.3288f12a.png) repeat
}

body #root{
  overflow-x:hidden
}

.title-wrapper{
  position:relative
}

.title-wrapper h1{
  position:absolute;
  bottom:10px;
  left:0
}

@media (min-width: 1250px){
  .title-wrapper{
    position:static;
    position:initial
  }

  .title-wrapper h1{
    position:static;
    position:initial
  }
}

.working-experiences{
  display:flex;
  flex-flow:row wrap
}

.working-experiences .last-exp{
  order:5
}

/* purgecss start ignore */

.hero-banner{
  scroll-snap-align:start;
  margin:auto;
  box-sizing:border-box;
  display:grid;
  height:100vh;
  max-width:1400px;
  grid-template-columns:repeat(7, minmax(0, 1fr))
}

@media (min-width: 768px){
  .hero-banner{
    grid-template-columns:repeat(12, minmax(0, 1fr))
  }
}

.hero-banner .cloud-image{
  position:relative;
  width:100%;
  height:550px
}

.hero-banner .cloud-image #triangle{
  position:absolute;
  bottom:60px;
  left:50%;
  -webkit-transform:translateX(-50%);
          transform:translateX(-50%)
}

.hero-banner .cloud-image #me{
  position:absolute;
  height:550px;
  top:-60px;
  left:50%;
  -webkit-transform:translateX(-50%);
          transform:translateX(-50%)
}

/* purgecss end ignore */

/* purgecss start ignore */

.custom-navbar{
  z-index:90;
  position:fixed;
  top:0;
  left:0;
  width:100vw;
  transition:background-color .3s linear
}

.custom-navbar.opaque{
  background:#333
}

.custom-navbar.opaque.light{
  background:#E0E0E0
}

.custom-navbar.opaque.dark{
  background:#333
}

.custom-navbar .main-container{
  display:flex;
  height:100%;
  flex-direction:row;
  flex-wrap:wrap;
  align-items:center;
  justify-content:space-between;
  padding:1rem
}

.custom-navbar .main-container .right-container,.custom-navbar .main-container .left-container{
  flex:1 1 50px;
  max-width:33%
}

.custom-navbar .main-container .right-container{
  text-align:right
}

.custom-navbar .main-container .left-container{
  text-align:left
}

.nav-menu{
  display:none;
  flex-flow:row;
  -webkit-padding-start:0;
          padding-inline-start:0;
  justify-content:space-around
}

@media (min-width: 768px){
  .nav-menu{
    display:flex
  }
}

.nav-menu.vertical{
  flex-flow:column
}

.nav-menu-item{
  flex:0 0 20%;
  list-style-type:none;
  text-align:center
}

.nav-menu-item a{
  color:#4F4F4F;
  font-size:18px;
  line-height:27px;
  font-weight:bold;
  text-decoration:none
}

